// app/page.tsx
"use client";
import { useState } from "react";
import { loginAs } from "@/lib/auth";

export default function LoginPage() {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [role, setRole] = useState("student");

  const handleSubmit = (e : any) => {
    e.preventDefault();
    loginAs(role); // saves role to localStorage
  };

  return (
    <div className="flex items-center justify-center min-h-screen bg-[#F9FAFB]">
      <div className="bg-white p-8 rounded-2xl shadow-lg w-full max-w-md">
        <h1 className="text-2xl font-bold text-[#0B3D2E] mb-2 text-center">
          AFUED Portal Login
        </h1>
        <p className="text-sm text-gray-500 mb-6 text-center">
          Welcome back! Please enter your details.
        </p>

        <form onSubmit={handleSubmit} className="space-y-4">
          <div>
            <label className="block text-sm text-gray-700 mb-1">Email</label>
            <input
              type="email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              className="w-full border border-[#D1D5DB] rounded-lg p-2 focus:ring-2 focus:ring-[#0B3D2E] outline-none"
              required
            />
          </div>

          <div>
            <label className="block text-sm text-gray-700 mb-1">Password</label>
            <input
              type="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              className="w-full border border-[#D1D5DB] rounded-lg p-2 focus:ring-2 focus:ring-[#0B3D2E] outline-none"
              required
            />
          </div>

          <div>
            <label className="block text-sm text-gray-700 mb-1">
              Select Role
            </label>
            <select
              value={role}
              onChange={(e) => setRole(e.target.value)}
              className="w-full border border-[#D1D5DB] rounded-lg p-2 focus:ring-2 focus:ring-[#0B3D2E] outline-none"
            >
              <option value="student">Student</option>
              <option value="lecturer">Lecturer</option>
              <option value="admin">Admin</option>
            </select>
          </div>

          <button
            type="submit"
            className="w-full bg-[#0B3D2E] text-white py-2 rounded-lg hover:bg-[#11543E] transition"
          >
            Login
          </button>

          <p className="text-sm text-center text-gray-500 mt-3">
            Donâ€™t have an account?{" "}
            <a href="/signup" className="text-[#0B3D2E] font-semibold">
              Sign up
            </a>
          </p>
        </form>
      </div>
    </div>
  );
}
